import { Component, Prop, h, Host } from '@stencil/core';

export interface TooltipItem {
  color?: string;
  label?: string;
  value: string;
}

@Component({
  tag: 'bh-chart-tooltip',
  styleUrl: 'bh-chart-tooltip.css',
  shadow: true,
})
export class BhChartTooltip {
  /**
   * Whether the tooltip is visible
   */
  @Prop() visible: boolean = false;

  /**
   * X position of the tooltip (in pixels)
   */
  @Prop() x: number = 0;

  /**
   * Y position of the tooltip (in pixels)
   */
  @Prop() y: number = 0;

  /**
   * Header text for the tooltip
   */
  @Prop() header: string = '';

  /**
   * Items to display in the tooltip
   */
  @Prop() items: TooltipItem[] = [];

  /**
   * Total value to display
   */
  @Prop() total: string = '';

  /**
   * Whether to show the total
   */
  @Prop() showTotal: boolean = false;

  /**
   * Whether in storybook mode (for static positioning)
   */
  @Prop() storybookMode: boolean = false;

  render() {
    return (
      <Host>
        <div
          class={{
            'chart-tooltip': true,
            visible: this.visible,
            'storybook-mode': this.storybookMode,
          }}
          style={{
            left: `${this.x}px`,
            top: `${this.y}px`,
          }}
        >
          <div class="tooltip-content">
            {this.header && <div class="tooltip-header">{this.header}</div>}
            <div class="tooltip-items">
              {this.items.map((item, index) => (
                <div key={index} class="tooltip-item">
                  {item.color && (
                    <span
                      class="tooltip-dot"
                      style={{ backgroundColor: item.color }}
                    />
                  )}
                  {item.label && (
                    <span class="tooltip-label">{item.label}:</span>
                  )}
                  <span class="tooltip-value">{item.value}</span>
                </div>
              ))}
            </div>
            {this.showTotal && this.total && (
              <div class="tooltip-total">
                <span class="total-label">Total:</span>
                <span class="total-value">{this.total}</span>
              </div>
            )}
          </div>
        </div>
      </Host>
    );
  }
}
